export class PageParams {
  // 总页数
  public totalPages = 1;
  // 总条数
  public totalElements = 0;
  // 当前页条数
  public numberOfElements = 0;
  // 是否首页
  public first = true;
  // 是否末页
  public last = true;
  // 每页条数
  public size = 0;
  // 第几页
  public number;
}



